
<template>
  <div>
    <p>Json Sort</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <textarea v-model="source" id="source" rows="50" cols="120" placeholder="source text"></textarea>
    <textarea v-model="output" id="output" rows="50" cols="120" placeholder="output" readonly></textarea>
    <br>
    <div>
      <input value="asc handle" type="button" @click="handle('asc')">
      <input value="desc handle" type="button" @click="handle('desc')">
    </div>
  </div>
</template>

<script>

export default {
  name: 'JsonSortView',
  data () {
    return {
      source: '',
      output: ''
    }
  },
  methods: {
    handle (sort) {
      const json = JSON.parse(this.source)
      const keyArr = Object.keys(json)
      if (sort === 'desc') {
        keyArr.sort((a, b) => b.localeCompare(a))
      } else {
        keyArr.sort((a, b) => a.localeCompare(b))
      }
      const target = {}
      keyArr.forEach(e => {
        target[e] = json[e]
      })
      this.output = JSON.stringify(target, null, 2)
    }
  }
}
</script>
